import React, { Component } from 'react'
import TodoInput from './TodoInput'
import TodoList from './TodoList'
import getDateTime from '../functons/getDateTime'

export default class Todo extends Component {
  constructor(props) {
    super(props)

    this.storage = 'app-state'
    const savedState = JSON.parse(localStorage.getItem(this.storage))

    // „Åô„Åß„Å´‰øùÂ≠ò„Åï„Çå„Åü„Éá„Éº„Çø„Åå„ÅÇ„ÇãÂ†¥Âêà„ÅØ„ÄÅ„Åù„Çå„Çí‰Ωø„ÅÜ
    // „Å™„ÅÑÂ†¥Âêà„ÅØ„ÄÅdefaultState „ÇíÂëº„Å∂
    this.state = savedState ? savedState : this.defaultState()
  }

  // „Éá„Éï„Ç©„É´„Éà„ÅÆ state „ÇíËøî„Åô
  defaultState = () => {
    const defaultTask = 'Add your tasks or edit this!'
    const defaultPriority = 'middle'

    return {
      tasks: [{
        task: defaultTask,
        before: defaultTask,
        status: false, // true => Done, false => NotYet
        editing: false, // true => „Çø„Çπ„ÇØ„ÇíÁ∑®ÈõÜ‰∏≠
        time: getDateTime(),
        priority: defaultPriority,
        id: 0
      }],
      uniquId: 1,
      input: {
        task: '',
        priority: defaultPriority
      }
    }
  }

  // state „Çí localStorage „Å´‰øùÂ≠ò„Åô„Çã
  saveState = state => {
    window.localStorage.setItem(this.storage, JSON.stringify(state))
  }

  handleSave = () => {
    this.saveState(this.state)
  }

  // localStorage „Å´‰øùÂ≠ò„Åï„Çå„Åü state „ÇíÂâäÈô§„Åô„Çã
  // „Éá„Éï„Ç©„É´„Éà„ÅÆ state „Å´Êàª„Åô
  removeState = () => {
    window.localStorage.removeItem(this.storage)
    this.setState(this.defaultState())
  }

  setTask = task => {
    const input = this.state.input
    input.task = task
    this.setState({
      input
    }, this.handleSave)
  }

  setPriority = priority => {
    const input = this.state.input
    input.priority = priority
    this.setState({
      input
    }, this.handleSave)
  }

  // Todo „ÇíËøΩÂä†
  addTodo = todo => {
    todo.task = todo.task.trim()
    if (todo.task === '') return

    const { tasks, uniquId } = this.state

    tasks.unshift({
      task: todo.task,
      before: todo.task,
      status: false,
      editing: false,
      time: getDateTime(),
      priority: todo.priority,
      id: uniquId
    })

    this.setState({
      tasks,
      uniquId: uniquId + 1
    }, this.handleSave)
  }

  // „Ç¥„ÉüÁÆ±„Ç¢„Ç§„Ç≥„É≥üóë„Çí„ÇØ„É™„ÉÉ„ÇØ„Åó„Å¶„ÄÅ„Çø„Çπ„ÇØ„ÇíÂâäÈô§„ÄÇ
  removeTodo = id => {
    this.setState({
      tasks: this.state.tasks.filter(task => task.id !== id)
    }, this.handleSave)
  }

  // Todo „ÇíÂÆå‰∫Ü„Å´„Åô„Çã
  doneTodo = id => {
    this.setState(this.state.tasks.map(task => {
      if (task.id === id) {
        task.status = !task.status
      }
      return task
    }), this.handleSave)
  }

  // „Çø„Çπ„ÇØ„ÅÆÁ∑®ÈõÜ„Çí‰øùÂ≠ò„Åô„Çã
  updatedTask = id => {
    this.setState(this.state.tasks.map(task => {
      if (task.id === id) {
        task.editing = false
        task.before = task.task
      }

      return task
    }), this.handleSave)
  }

  // „Çø„Çπ„ÇØ„ÅÆÁ∑®ÈõÜ„Çí„Ç≠„É£„É≥„Çª„É´„Åó„Å¶„ÄÅÂÖÉ„ÅÆ„Çø„Çπ„ÇØ„ÇíË°®Á§∫„Åô„Çã
  didNotUpdateTask = id => {
    this.setState(this.state.tasks.map(task => {
      if (task.id === id) {
        task.editing = false
        task.updated = !task.updated
        task.task = task.before
      }

      return task
    }), this.handleSave)
  }

  // „Çø„Çπ„ÇØ„ÇíÁ∑®ÈõÜ„Åô„Çã
  changeTask = (id, value) => {
    this.setState(this.state.tasks.map(task => {
      if (task.id === id) {
        task.editing = true
        task.task = value
      }

      return task
    }), this.handleSave)
  }

  // ÊäïÁ®øÊ∏à„Åø„ÅÆ„Çø„Çπ„ÇØ„ÅÆ Priority „ÇíÂ§âÊõ¥„Åô„Çã
  changePriority = (id, priority) => {

    this.setState(this.state.tasks.map(task => {
      if (task.id === id) {
        task.priority = priority
        task.changingPriority = false
      }

      return task
    }), this.handleSave)
  }

  render() {
    const props = {
      tasks: this.state.tasks,
      removeTodo: this.removeTodo,
      doneTodo: this.doneTodo,
      editing: this.editingTodo,
      changeTask: this.changeTask,
      updatedTask: this.updatedTask,
      didNotUpdateTask: this.didNotUpdateTask,
      changePriority: this.changePriority,
    }

    return (
      <div className="todo">
        <TodoInput addTodo={this.addTodo} setPriority={this.setPriority} setTask={this.setTask} input={this.state.input} />
        <TodoList {...props} />
        <p className="remove-data">
          This app uses localStorage to store ToDo data.<br />
          <button className="remove-data__button" onClick={() => this.removeState()}>Clear the data</button>
        </p>
      </div>
    )
  }
}
